/*
  Hover: Text 
 
  hovers can easily be added to objects

  hover_color_primary              - text color modified light to dark
  hover_color_secondary              - text color modified light to dark
  hover_color_info              - text color modified light to dark
  hover_color_highlight              - text color success color
  hover_color_success              - text color modified light to dark
  hover_color_warning              - text color modified light to dark
  hover_color_alert              - text color modified light to dark

  markup:
  <div class="grid-x margin_5">
    <div class="{$modifiers}-5 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">5</div>
    <div class="{$modifiers}-4 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">4</div>
    <div class="{$modifiers}-3 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">3</div>
    <div class="{$modifiers}-2 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">2</div>
    <div class="{$modifiers}-1 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">1</div>
    <div class="{$modifiers}-0 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1">0</div>
    <div class="{$modifiers}-n1 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1 ">n1</div>
    <div class="{$modifiers}-n2 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1 ">n2</div>
    <div class="{$modifiers}-n3 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1 ">n3</div>
    <div class="{$modifiers}-n4 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1 ">n4</div>
    <div class="{$modifiers}-n5 cell auto font_bold back_secondary-3 font_4 font_slab text-center border-width_1 border-solid border_secondary-4 padding_1 ">n5</div>
  </div>

 
  Styleguide 109.2
 
 */
.hover_color_white.hover_color_black, .hover_black, .hover_white {
  transition: all ease-in 0.5s;
}

.hover_color_white:hover, .hover_white:hover {
  color: white;
}

.hover_color_black:hover, .hover_black:hover {
  color: black;
}

