/*
 UC: Borders

  Borders a complex utility that demands a combo UCs to acheive their effect. The three UCs that must be used in combo are 'border_X','border-width_X', and 'border-color_X'.  So to make an object with single black pixel border that is solid you would have to add '.border_solid.border-width_1.border_black' 

  Styleguide 103.0
 
 */
/*
	Border Type

	Depends on base border module in _borders.css

	border_none           - none
	border_dotted         - dotted
	border_dashed         - dashed
	border_solid          - solid

	markup:
	<div class="{$modifiers} border_black border-width_1 padding_3">{$modifiers} border</div>

	Styleguide 103.1

 */
.border_dotted, .border_dashed, .border_solid, .border_none {
  border-width: 0;
}

.border_dotted {
